module.exports = "#sidebar h4 {\n    margin-top: 40px;\n    color: white;\n}\n\n#sidebar p {\n    color: #e25822;\n}\n\n#sidebar {\n    width: 200px;\n    height: 600px;\n    background: rgba(255, 255, 255, 0.7);\n    top: 10%;\n    position: absolute;\n    z-index: 1000;\n    padding: 10px 15px;\n    font: 14px/16px Arial, Helvetica, sans-serif;\n    text-align: center;\n    display: block;\n}\n\n.switch {\n    position: relative;\n    display: inline-block;\n    width: 60px;\n    height: 34px;\n}\n\n/* Hide default HTML checkbox */\n\n.switch input {\n    opacity: 0;\n    width: 0;\n    height: 0;\n}\n\n/* The slider */\n\n.slider {\n    position: absolute;\n    cursor: pointer;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background-color: #ccc;\n    transition: .4s;\n}\n\n.slider:before {\n    position: absolute;\n    content: \"\";\n    height: 26px;\n    width: 26px;\n    left: 4px;\n    bottom: 4px;\n    background-color: white;\n    transition: .4s;\n}\n\ninput:checked + .slider {\n    background-color: #e25822;\n}\n\ninput:focus + .slider {\n    box-shadow: 0 0 1px #e25822;\n}\n\ninput:checked + .slider:before {\n    -webkit-transform: translateX(26px);\n    transform: translateX(26px);\n}\n\n/* Rounded sliders */\n\n.slider.round {\n    border-radius: 34px;\n}\n\n.slider.round:before {\n    border-radius: 50%;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"